<template>
  <div>
    <div id="header">
      <header-nav></header-nav>
    </div>

    <div class="content mt30 clearfix padding-top-60" id="content">
      <!--左边区域-->
      <div id="left" class="mr30 mt30 mb30">
        <!--封面图-->
        <div class="bg-img mainImg br5" :style="{backgroundImage:'url('+osshost+ swiperList[0] +detailpic+')'}"
             v-if="swiperList.length==1"></div>
        <el-carousel height="456px" v-else>
          <el-carousel-item v-for="(item,index) in swiperList" :key="index">
            <div class="bg-img mainImg br5" :style="{backgroundImage:'url('+osshost+ item +detailpic+')'}"></div>
          </el-carousel-item>
        </el-carousel>
        <!--用户信息-->
        <div>
          <ul class="dr_list dr_list2 mt20">
            <li class="clearfix mb15">
              <div class="left headicon bg-img br50"
                   @click="navToIndex(dtDetail.userId)"
                   :style="{backgroundImage:'url('+osshost+ dtDetail.faceimg +userface+')'}">
              </div>
              <div class="info">
                <h2 class="text-bold text-df">{{dtDetail.nickname}}</h2>
              </div>
              <a class="right" v-if="dtDetail.relationship == 0 || dtDetail.relationship == 2"
                 @click="attentionUser(dtDetail.userId)">
                <el-button class="el-icon-plus" size="small" type="warning" round>关注</el-button>
              </a>
              <a class="right" v-else @click="unAttentionUser(dtDetail.userId)">
                <el-button class="el-icon-close" size="small" round>取消关注</el-button>
              </a>
            </li>
          </ul>
          <div class="text-df">{{dtDetail.description}}</div>
        </div>
        <div class="mt50">
          <div class="dzuser" v-for="(item, index) in dtDetail.like_people_list" :key="index"
               @click="navToIndex(item.userid)">
            <el-avatar :src="osshost+ item.faceimg +userface"></el-avatar>
          </div>

          <el-badge :value="dtDetail.like_num" class="right">
            <el-button v-if="dtDetail.isliked==false" size="small" type="success" round class="el-icon-lollipop"
                       @click="pushdz()">送颗糖给TA
            </el-button>
            <el-button v-if="dtDetail.isliked==true" size="small" type="info" round @click="unpushdz()">把糖拿回来
            </el-button>
          </el-badge>
        </div>

        <div class="mt50" v-if="dtDetail.relation_item">
          <h1>关联菜谱</h1>
          <div class="tjcp-box">

            <div class="left tjcp-left">
              <div class="food-img"
                   :style="{ backgroundImage: 'url('+osshost + dtDetail.relation_item.cpimg +detailpic+ ')' }"
                   @click="navToDetail(dtDetail.relation_item.id)"></div>
            </div>
            <div class="left tjcp-right ml10">
              <div class="mt10">
                <span class="text-bold text-20 text-cut">{{ dtDetail.relation_item.title }}</span>
              </div>
              <div class="grayColor mt10">
                <span class="text-12" v-for="(item, index) in dtDetail.relation_item.stuff" :key="index">{{ item }}<i
                  v-if="(index+1)!=dtDetail.relation_item.stuff.length">、</i></span>
              </div>
            </div>

          </div>
        </div>
        <div class="mt50">
          <div class="text-df mt20">
            <p class="grayColor">创建时间：{{dtDetail.pushtime}}</p>
          </div>
        </div>
        <!--面包屑导航  -->
        <div class="">
          <p class="fcc text-lips">您当前的位置：<a href="/" title="">首页</a> \ {{dtDetail.description}}</p>
        </div>
        <!-- 这里是 评论-->
        <div class="mt50">
          <h1 id="reply">评论<el-button class="right" type="warning" @click="comment(0, 0)">发表评论</el-button></h1>
          <div class="mt30">

            <el-dialog
              title="评论回复"
              :visible.sync="replyShow"
              width="600px"
              :before-close="handleClose">
              <form class="cu-form-group">
                <textarea :placeholder="input_placeholder" v-model="message"></textarea>
                <div class="clearfix">
                  <button class="cu-btn sm bg-yellow right br5 mt10"
                          @click="postComment()"> 发表评论
                  </button>
                </div>
              </form>
            </el-dialog>
            <div>
              <div>
                <ul class="dr_list">
                  <li class="clearfix mb15" v-for="(item, index) in commentList" :key="index">
                    <div class="left headicon bg-img br50"
                    @click="navToIndex(item.userid)"
                         :style="{backgroundImage:'url('+ osshost + item.faceimg + userface +')'}">
                    </div>
                    <div class="info" style="width:640px">
                      <h4 class="text-bold text-df">{{ item.nickname }}<span class="text-12 grayColor user-img-boder ml10" v-if="item.userid == dtDetail.userId">作者</span></h4>
                      <p class="grayColor">{{ item.pushtime }} <span class="mainColor"
                                                                     @click="reply(item.commentid,item.userid,item.id,item.nickname)">回复</span></p>

                      <div>
                        <p class="text-df text-height-lg"> {{ item.message }}</p>
                      </div>
                      <ul class="dr_list">
                        <li class="clearfix mb15" v-for="(child, childindex) in item.subreplylist" :key="childindex">
                          <div class="left headicon bg-img br50"
                               :style="{backgroundImage:'url('+ osshost + child.faceimg + userface +')'}"
                               @click="navToIndex(child.userid)">
                          </div>
                          <div class="info" style="width:440px">
                            <h4 class="text-bold text-df">{{ child.nickname }}<span class="text-12 grayColor user-img-boder ml10" v-if="child.userid == dtDetail.userId">作者</span></h4>
                            <p class="grayColor">{{ child.pushtime }} <span class="mainColor"
                                                                            @click="reply2(child.id, item.userid, child.userid, child.commentid, child.nickname)">回复</span>
                            </p>
                            <div>
                              <p class="text-14 grayColor" v-if="child.bereplynickname">回复:{{ child.bereplynickname }}</p>
                              <p class="text-df text-height-lg"> {{ child.message }}</p>
                            </div>
                          </div>
                        </li>
                      </ul>
                    </div>
                  </li>
                </ul>
                <div class="text-center mainColor mb15" v-if="loadMore" @click="loadMorep()">点击加载更多</div>
                <div class="text-center grayColor mb15" v-else>没有评论了</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--右边区域-->
      <div id="right" class="mt30">
        <right-view :uid="dtDetail.userId" v-if="dtDetail.userId"></right-view>
      </div>
    </div>
    <FooterBottom></FooterBottom>
    <Totop></Totop>
  </div>


</template>

<script>
  import headerNav from "@/components/headerNav";
  import RightView from "../components/rightView";
  import FoodItem from "@/components/foodItem";
  import Totop from '@/components/totop.vue';
  import FooterBottom from "@/components/footer";

  import {getActionDetail, pushLike, pushUnlike} from '@/api/action'
  import {commentList, pushReply,likeMenu,unlikeMenu} from '@/api/operate'
  import {Message} from "element-ui";

  export default {
    inject: ['reload'],
    components: {
      RightView,
      FoodItem,
      headerNav,
      FooterBottom,
      Totop
    },
    data() {
      return {
        num: 13,
        osshost: process.env.UPLOAD_URL,
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
        sortList: '',
        dtDetail: [],
        loadMore: true,
        token: '',
        commentList: [],
        replyShow: false,
        reply2Show: false,
        input_placeholder: '来了说两句吧~',
        is_reply: false,
        page: 1,
        beuserid: 0,
        message: '',
        replyid: 0,
        replyuserid: 0,
        commentid: 0,
        message1: '',
        message2: '',
        did: '',
        swiperList:[],
      }
    },
    created() {
      this.token = this.$store.state.user.token || '';
      this.did = this.$route.query.id;
      this.getDataInfo(this.did);
      this.getCommentList();
    },
    methods: {
      getDataInfo(did) {
        getActionDetail(did).then(response => {
          let res = response.data;
          if (res.success) {
            this.dtDetail = res.result;
            this.swiperList = this.dtDetail.dtimg.split('|');
            this.dtDetail.relation_item.stuff = this.dtDetail.relation_item.stuff.split('|');
            for (var i = 0; i < this.dtDetail.relation_item.stuff.length; i++) {
              let array = [];
              array = this.dtDetail.relation_item.stuff[i].split('#');
              this.dtDetail.relation_item.stuff[i] = array[0]
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      pushdz() {
        if (this.token) {
          likeMenu(this.did, 1).then(response => {
            let res = response.data;
            if (res.success) {
              Message({
                message: '你送了一根棒棒糖',
                type: 'success',
                duration: 5 * 1000
              })
              this.reload();
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
          })
        }
      },
      unpushdz() {
        if (this.token) {
          unlikeMenu(this.did, 1).then(response => {
            let res = response.data;
            if (res.success) {
              Message({
                message: '你把棒棒糖要回来了',
                type: 'success',
                duration: 5 * 1000
              })
              this.reload();
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
          })
        }
      },
      getCommentList() {
        commentList(this.did, 1, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              this.commentList = this.commentList.concat(res.result);
            } else {
              this.loadMore = false;
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      loadMorep(){
          this.page++;
          this.getCommentList()
      },
      // reply(nickname) {
      //   this.is_reply = true; //回复中
      //   this.replyShow = true; //调起input框
      //   let replyTo = nickname;
      //   this.input_placeholder = '回复' + replyTo;
      // },
      comment(userid, commentid) {
      	//评论
      	if (this.token == '') {
      		navigateTo(`../login/pswLogin`);
      	} else {
      		this.replyShow = true; //调起input框
      		this.beuserid = userid;
      		this.commentid = commentid;
      	}
      },
      reply(commentid,userid,id,nickname) {
      	this.is_reply = true; //回复中
      	this.replyShow = true; //调起input框
      	let replyTo = nickname;
      	this.input_placeholder = '回复' + replyTo;
      	this.replyid = 0;
        this.replyuserid = 0;
      	this.beuserid = userid;
      	this.commentid = id;
        console.log(this.commentid,this.beuserid);
      },
      reply2(id, topuserid, userid, commentid, nickname) {
      	this.is_reply = true; //回复中
      	this.replyShow = true; //调起input框
      	let replyTo = nickname;
      	this.input_placeholder = '回复' + replyTo;
      	this.replyid = id;
      	this.beuserid = topuserid;
      	this.replyuserid = userid;
      	this.commentid = commentid;
      },
      postComment() {
        if (this.token == '') {
          this.$router.push({path: "/login"})
        } else {
          let _this = this;
          let meassage = _this.message;
          let params = {
          	itemid: _this.did,
          	itemtype: 1,
          	replyid: _this.replyid,
          	replyuserid: _this.replyuserid,
          	commentid: _this.commentid,
          	beuserid: _this.beuserid,
          	message: _this.message
          };
          pushReply(params).then(response => {
            let res = response.data;
            if (res.success) {
              Message({
                message: res.message,
                duration: 5 * 1000
              })
              document.getElementById("reply").scrollIntoView();
              this.page = 0;
              this.commentList = [];
              this.getCommentList()
              this.is_reply = false; //回复中
              this.replyShow = false; //调起input框
            } else {
              Message({
                message: res.message,
                type: 'error',
                duration: 5 * 1000
              })
              if (res.code == '9999') {
                this.$router.push({path: "/login"})
              }
            }
          }).catch(error => {
            console.log(error)
          })
        }

      },
      attentionUser(userid) {
        if (this.token) {
          followUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.dtDetail.relationship == 0) {
                this.dtDetail.relationship = 1;
              } else if (this.dtDetail.relationship == 2) {
                this.dtDetail.relationship = 3;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
      unAttentionUser(userid) {
        if (this.token) {
          unfollowUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.dtDetail.relationship == 1) {
                this.dtDetail.relationship = 0;
              } else if (this.dtDetail.relationship == 3) {
                this.dtDetail.relationship = 2;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
      navToIndex(id) {
        this.$router.push({path: "/user", query: {id}})
      },
      navToDetail(id) {
        this.$router.push({path: "/cookBook", query: {id}})
      },
    }
  }

</script>

<style scoped>
  .mainImg {
    width: 685px;
    height: 456px;
  }
  .tjcp-left{
    width: 280px;
        height: 190px;
  }
</style>



